@import url('https://fonts.googleapis.com/css?family=Muli&display=swap');
@import url('https://fonts.googleapis.com/css?family=Montserrat:400,700&display=swap');

* {
	box-sizing: border-box;
}

body {
  background: radial-gradient(#e5e5e5, #ffff, #e5e5ee);
	font-family: 'Monserrat', sans-serif;
	min-height: 100vh;
	margin-bottom: 50px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.main {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin: 0 auto 30px;
}

.text-center {
	text-align: center;
}

.container {
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 10px auto;
	position: relative;
	max-width: 90vw;
	width: 800px;
}

.or{
  z-index: 1;
}
.container .or {
	background-color: #fff;
	border-radius: 50%;
	color: #ff416c;
	display: flex;
	font-size: 1.3em;
	align-items: center;
	justify-content: center;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -140%);
	padding: 10px;
	margin: 0;
	height: 50px;
	width: 50px;
}

.container .name {
	margin: 10px 0 20px;
}

.img-container {
	background-color: #fff;
	box-shadow: 5px 10px 20px 1px rgba(0,0,0,0.253) ;
	border-radius: 5px;
	padding: 0 0 10px;
	margin: 0 3px;
	height: 450px;
	width: 50%;
}

.img-container img {
	height: 75%;
	width: 100%;
	object-fit: cover;
}
